.profile__avatar-overlay {
    width: 120px;
    height: 120px;
    background: rgba(0, 0, 0, 0.8);
    opacity: 0;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile__avatar-overlay:hover {
    opacity: 0.8;
    transition: .5s linear;
}